* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.flex {
    display: flex!important;
}
.size-icon {
    font-size: 25px!important;
}
body {
    background-image: linear-gradient(120deg, #657cf6 0%, #fda085 100%);
    color: white;
    font-family: "Poppins", sans-serif;
    min-height: 100vh;
}
header {
    font-size: 2rem;
}

header,
form {
    min-height: 20vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
form input,
form button {
    padding: 0.5rem;
    font-size: 2rem;
    border: none;
    background: white;
}
form input {
    border-bottom-left-radius: 10px;
    border-top-left-radius: 10px;
}
form button {
    color: #73c7bc;
    background: #f7fffe;
    cursor: pointer;
    transition: all 0.3s ease;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}
form button:hover {
    background: #73c7bc;
    color: white;
}
.todo-input {
    padding: 0.5rem;
    font-size: 2rem;
    border: none;
}
.todo-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.todo-list {
    min-width: 35%;
    max-width: 70%;
    list-style: none;
}

.todo {
    margin: 0.5rem;
    font-size: 2rem;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: all 1s ease;
    background-image: linear-gradient(120deg, #acdbb8 0%, #39b3bf 100%);
    border-radius: 10px;
}
.filter-todo {
    padding: 1rem;
}
.todo li {
    flex: 1;
}
.change-btn,
.trash-btn,
.complete-btn {
    background: 0;
    color: white;
    border: none;
    padding: 1rem;
    cursor: pointer;
    font-size: 1rem;
}
.complete-btn {
    background: 0;
}

.check-mark {
    color: #7eff4c;
}

.change-btn{
    background: 0;
}
.todo-item {
    padding: 0.2rem 0.5rem 0 0.5rem;
}
.date-created {
    font-size: 15px;
    color: #f7fffe;
    padding: 1rem 0.5rem 0.5rem 0.5rem;
}

.fa-trash,
.fa-check {
    pointer-events: none;
}

.fall {
    transform: translateY(10rem) rotateZ(20deg);
    opacity: 0;
}

.completed {
    text-decoration: line-through #fa6666;
    opacity: 0.85;
}


select {
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    appearance: none;
    outline: 0;
    box-shadow: none;
    border: 0 !important;
    background-image: none;
}

.select {
    margin: 1rem;
    position: relative;
    overflow: hidden;
}
select {
    color: #ff6f47;
    font-family: "Poppins", sans-serif;
    cursor: pointer;
    width: 12rem;
}
/* Arrow */
.select::after {
    content: "\25BC";
    position: absolute;
    top: 0;
    right: 0;
    padding: 1rem;
    background: #ff6f47;
    cursor: pointer;
    pointer-events: none;
}